p{
  text-align: center;
  text-transform: uppercase;
  font-size: 20px;
}

.game-area{
  position: relative;
  margin: 25px auto;
  width: 600px;
  height: 600px;
  border: 2px solid #000;
}

.snake-dot{
  position: absolute;
  width: 12px;
  height: 12px;
  background-color: #000;
  border: 1px solid #fff;
  z-index: 2;
}

.snake-food{
  position: absolute;
  width: 12px;
  height: 12px;
  background-color: red;
  border: 1px solid #fff;
  z-index: 1;
}